import { useState }   from "react";
import { connect }    from "react-redux";
import { useHistory } from "react-router-dom";

//Our Imports
import { bindAll }                        from "Helpers";
import { useChangeRequestStatus, useCan } from "Helpers/Hooks";
import { ModalSlice }                     from "Store/Slices";
import { genericApi }                     from "Store/api/genericApi";
import RequestTable                       from "./RequestTable";

const RequestTableContainer = ({
	_id,
	filters,
	type_shf,
	modalSliceActions,
}) => {

	const [ can ] = useCan();
	const history = useHistory();

	const [page, setPage]                 = useState(1);
	const [dropdownOpen, setDropdownOpen] = useState({});

	const { data } = genericApi.useGetDataQuery({
		module : "petitions",
		params : {
			page,
			[!type_shf ? undefined :
				(type_shf === "controller" ? "id_controller" : "id_valuer")] : type_shf && _id,
			...filters,
		},
	});

	const [ handleChangeStatus, { isLoading } ] = useChangeRequestStatus({ request : Object.values(dropdownOpen)?.[0] ?? {} });

	const handleShowRequestDetails = (id) => () => history.push(`details_request/${id}`);

	const toggleDropdown = (request) => (e) => {
		e.stopPropagation();
		const key = request._id;
		setDropdownOpen(prev => ({ [key] : { ...request, isOpen : !prev[key]?.isOpen } }));
	};

	const togglePaginator = () => (val) => setPage(val);

	const handleEditBtn = (id, type) => (e) => {
		e.stopPropagation();
		history.push(`create_request/${id}?type=${type}`);
	};

	const handleDeleteBtn = (request) => (e) => {
		e.stopPropagation();
		modalSliceActions.openModal({
			type  : "confirmationModal",
			props : {
				_id                  : request._id,
				identifier           : request.key,
				name                 : "Solicitud",
				title                : "Eliminar solicitud",
				confirmationQuestion : "¿Estás seguro de eliminar la solicitud?",
				bodyText             : "No podrás recuperar la solicitud después de eliminarla.",
				confirmLabel         : "Eliminar",
				actionToDispatch     : "deleteRequest",
			},
		});
	};

	const getLabelToShow = (val, defaultLabel = "Sin datos") => (val) ? val : defaultLabel;

	const addressFormatter = (address) => {
		if (typeof address !== "object") {
			return "Sin datos";
		}

		const addresFormatted = `${getLabelToShow(address.street, "*")}, #${getLabelToShow(address.outer_number, "*")} 
		${address?.inner_number ? `- ${address.inner_number}` : ""},
		${getLabelToShow(address.colony, "*")}, ${getLabelToShow(address.municipality, "*")}`;

		return addresFormatted.replace(/\s/g, "") === "*,#*,*,*" ? "Sin datos" : addresFormatted;
	};

	return (
		<RequestTable
			delegations={{
				can,
				isLoading,
				dropdownOpen,
				handleEditBtn,
				toggleDropdown,
				getLabelToShow,
				handleDeleteBtn,
				togglePaginator,
				addressFormatter,
				handleChangeStatus,
				handleShowRequestDetails,

				requests  : data?.docs ?? [],
				totalPage : data?.totalPages ?? 1,
			}}
		 />
	);
};

const mapDispatchToState = ({ _authSlice, _requestSlice }) => ({
	filters  : _requestSlice?.filters ?? {},
	_id      : _authSlice?.user?._id        ?? "",
	type_shf : _authSlice?.user?.type_shf   ?? "",
});

const mapDispatchToProps = bindAll({
	ModalSliceActions : ModalSlice.actions,
});

export default connect(mapDispatchToState, mapDispatchToProps)(RequestTableContainer);
