import classNames from "classnames";
import {
	Edit,
	Trash,
	ChevronRight,
} from "react-feather";
import {
	Table,
	Badge,
	Button,
	DropdownItem,
	DropdownMenu,
	DropdownToggle,
	ButtonDropdown,
	UncontrolledTooltip,
} from "reactstrap";

//Our Importations
import { isValidArray, dayjs } from "Helpers";
import Paginate                from "Components/Global/Paginate";
import UserImage               from "Components/Global/UserImage";
import "./RequestTable.scss";
import AvatarText              from "Components/Global/AvatarText";

const statusTypes = [
	{ label : "Captura",     value : 0 },
	{ label : "Seguimiento", value : 1 },
	{ label : "Registrada",  value : 2 },
];

const reqtypes = [
	[
		"Vivienda Nueva",
		"Vivienda Usada",
	],
	"Línea 3",
];

const RequestTable = ({
	delegations : {
		can,
		requests,
		totalPage,
		dropdownOpen,
		handleEditBtn,
		getLabelToShow,
		toggleDropdown,
		handleDeleteBtn,
		togglePaginator,
		addressFormatter,
		handleChangeStatus,
		handleShowRequestDetails,
	},
}) => {
	return (
		<div id="RequestTable">
			<Table responsive hover>
				<thead>
					<tr>
						<th className="title">
							Clave
						</th>
						<th className="title">
							valuador
						</th>
						<th className="title">
							controlador
						</th>
						<th className="title">
							tipo
							solicitud
						</th>
						<th className="title">
							solicitante
						</th>
						<th className="title">
							propietario
						</th>
						<th className="title">
							domicilio
						</th>
						<th className="title">
							estado
						</th>
						<th className="title">
							status
						</th>
						<th className="title">
							Fecha
						</th>
						<th className="title last-column">
							Acciones
						</th>
					</tr>
				</thead>
				<tbody>
					{
						isValidArray(requests) &&
							requests.map(request => (
								<tr
									key={request?._id}
									id={`tooltip-${request?._id}`}
									className={request?.subject && "table-danger"}
									onClick={handleShowRequestDetails(request?._id)}
								>
									<td>
										{request?.key}
									</td>
									<td>
										<div className="group">
											<UserImage
												rounded
												size={21}
												image={request?.valuer?.image?.location ?? ""}
												name={request?.valuer?.name ?? "-"}
											/>
											<p>{getLabelToShow(request?.valuer?.name)}</p>
										</div>
									</td>
									<td>
										<AvatarText
											size={21}
											image={request?.controller?.image?.location}
											name={request?.controller?.name ?? ""}
										/>
									</td>
									<td>
										<p
											className={classNames("request-type", {
												"request-type-color-1" : request?.housing_type === 0,
												"request-type-color-2" : request?.petition_type === 1,
											})}
										>
											{  request?.petition_type > 0 ?
												getLabelToShow(reqtypes[request?.petition_type])
												:
												getLabelToShow(reqtypes[0][request?.housing_type])}
										</p>
									</td>
									<td>
										<p>{getLabelToShow(request?.applicant?.name)}</p>
									</td>
									<td>
										<p>{getLabelToShow(request?.owner?.name)}</p>
									</td>
									<td>
										<p>{addressFormatter(request?.applicant?.address)}</p>
									</td>
									<td>
										<p>{getLabelToShow(request?.place?.name)}</p>
									</td>
									<td onClick={(e) => e.stopPropagation()}>
										<ButtonDropdown
											disabled={request?.status === 2 || can("update", "request", "!status")}
											toggle={toggleDropdown(request)}
											onClick={(e) => e.stopPropagation()}
											isOpen={dropdownOpen[request?._id]?.isOpen}
										>
											<DropdownToggle
												color="flat"
												className="btn-dropdown"
											>
												<Badge className={
													classNames("badge-style", {
														"badge-style-1" : request?.status === 1,
														"badge-style-2" : request?.status === 2 || can("update", "request", "!status"),
													})
												}>
													<p className="text">{statusTypes[request?.status ?? 0].label}</p>
													<ChevronRight size={16} />
												</Badge>
											</DropdownToggle>
											<DropdownMenu>
												{
													statusTypes.map(statusType => (
														statusType.value !== request?.status &&
														<DropdownItem
															key={statusType.value}
															tag="a"
															onClick={handleChangeStatus(request, statusType.value)}
														>
															{statusType.label}
														</DropdownItem>
													))
												}
											</DropdownMenu>
										</ButtonDropdown>
									</td>
									<td>
										<p>{dayjs(request?.createdAt).format("DD/MM/YYYY")}</p>
									</td>
									<td>
										<div className="actions-wrapper">
											<Button.Ripple
												color="flat-secondary"
												className="btn-icon rounded-circle"
												onClick={handleEditBtn(request?._id, request?.petition_type)}
											>
												<Edit className="edit-icon" size={16} />
											</Button.Ripple>
											<Button.Ripple
												color="flat-danger"
												className="btn-icon rounded-circle"
												onClick={handleDeleteBtn(request)}
											>
												<Trash size={16} />
											</Button.Ripple>
										</div>
										{
											request?.subject &&
												<UncontrolledTooltip placement="bottom" target={`tooltip-${request?._id}`}>
													{request?.subject}
												</UncontrolledTooltip>
										}
									</td>
								</tr>
							))
					}
				</tbody>
			</Table>
			<Paginate
				totalPage={totalPage}
				handleGetData={togglePaginator()}
			/>
		</div>
	);
};

export default RequestTable;
