import { useEffect }  from "react";
import { connect }    from "react-redux";
import { useHistory } from "react-router-dom";

//Our Importations
import { bindAll }      from "Helpers";
import { useDebounce }  from "Helpers/Hooks";
import { RequestSlice } from "Store/Slices";
import GenericSerivices from "Store/Services/GenericSerivices";
import RequestPage      from "./RequestPage";


const RequestPageContainer = ({
	date,
	requestActions,
}) => {
	const history = useHistory();

	const handleSearch = useDebounce((search) => handleChangeFilter("search", search), 700);

	const handleChangeFilter = (filter, val) => {
		const queryElement = filter !== "date" ? val : JSON.parse(JSON.stringify(val));

		requestActions.setFilters({ [filter] : queryElement });
	};

	const handleOnClickHeaderBtn = (type) => () => history.push(`create_request/id?type=${type}`);

	useEffect(() => {
		return () => {
			requestActions.clearFilters();
		};
	  }, []);

	return (
		<RequestPage
			delegations={{
				date,
				handleSearch,
				handleChangeFilter,
				handleOnClickHeaderBtn,
			}}
		/>
	);
};

const mapDispatchToState = ({ _requestSlice }) => ({
	date : _requestSlice?.filters?.date ?? undefined,
});

const mapDispatchToProps = bindAll({ GenericSerivices, RequestActions : RequestSlice.actions });

export default connect(mapDispatchToState, mapDispatchToProps)(RequestPageContainer);
