import { connect } from "react-redux";
import { Button }  from "reactstrap";
import { X }       from "react-feather";
import Flatpickr   from "react-flatpickr";
import { Spanish } from "flatpickr/dist/l10n/es";
import "@styles/react/libs/flatpickr/flatpickr.scss";

// Our Imports
import HeaderPage           from "Components/Global/HeaderPage";
import RequestTable         from "Components/Request/RequestTable";
import FormAsyncSelectGroup from "Components/Global/FormAsyncSelectGroup";
import FormSelectGroup      from "Components/Global/FormSelectGroup";
import "./RequestPage.scss";


const btnHeaderOptions = [
	{
		label : "Linea 2",
		value : 0,
	},
	{
		label : "Linea 3",
		value : 1,
	},
];

const statusOptions = [
	{
		label : "Capturada",
		value : 0,
	},
	{
		label : "Seguimiento",
		value : 1,
	},
	{
		label : "Registrada",
		value : 2,
	},
];

const requestTypeOptions = [
	{
		label : "Vivienda Nueva",
		value : 0,
	},
	{
		label : "Vivienda Usada",
		value : 1,
	},
	{
		label : "Linea 3",
		value : 2,
	},
];

const RequestPage = ({
	type_shf,
	delegations : {
		date,
		handleSearch,
		handleChangeFilter,
		handleOnClickHeaderBtn,
	},
}) => {
	return (
		<div id="RequestPage">
			<HeaderPage
				title="Solicitudes"
				btnLabel="Nueva Solicitud"
				handleOnclick={handleOnClickHeaderBtn}
				handleSearch={handleSearch}
				btnOptions={btnHeaderOptions}
			/>
			<div className="filters-container mb-2">
				{
					type_shf !== "valuer" &&
					<FormAsyncSelectGroup
						module="users"
						name="valuer"
						placeholder="Valuador"
						query={{  typeShf : "valuer" }}
						onChange={(e) => handleChangeFilter("id_valuer", e?.value)}
					/>
				}
				{
					type_shf !== "controller" &&
					<FormAsyncSelectGroup
						module="users"
						name="controller"
						placeholder="Controlador"
						query={{  typeShf : "controller" }}
						onChange={(e) => handleChangeFilter("id_controller", e?.value)}
					/>
				}
				<FormSelectGroup
					name="status"
					isClearable
					options={statusOptions}
					placeholder="Estado"
					className="react-select"
					classNamePrefix="select"
					onChange={(e) => handleChangeFilter("status", e?.value)}
					menuPortalTarget={document.body}
				/>
				<FormSelectGroup
					name="petition_type"
					isClearable
					options={requestTypeOptions}
					placeholder="Tipo de Solicitud"
					className="react-select"
					classNamePrefix="select"
					onChange={(e) => handleChangeFilter("petition_type", e?.value)}
					menuPortalTarget={document.body}
				/>
				<div className="flatpickr-clear-btn">
					{
						date &&
							<Button
								size="sm"
								color="flat-secondary"
								className="btn-icon rounded-circle btn-icon-flatpickr"
								onClick={() => handleChangeFilter("date", "")}
							>
								<X size={12} />
							</Button>
					}
					<Flatpickr
						value={date}
						onChange={([e]) => handleChangeFilter("date", e)}
						placeholder="Filtrar por fecha"
						className="form-control flatpickr-input intp-flatpick"
						options={{ locale : Spanish, dateFormat : "d/m/Y" }}
					/>
				</div>
			</div>
			<RequestTable />
		</div>
	);
};

const mapDispatchToState = ({ _authSlice }) => ({
	type_shf : _authSlice?.user?.type_shf ?? "",
});

export default connect(mapDispatchToState)(RequestPage);
